
import "./index.scss";

import { history } from "&/history"

export default class Experience extends Component{
    gotoIndex=()=>{
        history.push("./home")
    }
    render(){
        return(
            <div className="experience">
                <h1 className="logo_top">
                    <img src="http://c-shuqi.11222.cn/assets/download/logo_157cda5.png" alt=""/>
                </h1>
                <p className="img_txt">
                    <img src="http://c-shuqi.11222.cn/assets/download/p1-title-ios_bdefd1f.png" alt=""/>
                </p>
                <div className="img_nav">
                    <ul>
                        <li>
                            <img src="http://c-shuqi.11222.cn/assets/download/p1-1_6eb9d0f.png" alt=""/>
                        </li>
                        <li>
                            <img src="http://c-shuqi.11222.cn/assets/download/p1-2_26ed9ab.png" alt=""/>
                        </li>
                        <li>
                            <img src="http://c-shuqi.11222.cn/assets/download/p1-3_a207ab6.png" alt=""/>
                        </li>
                        <li>
                            <img src="http://c-shuqi.11222.cn/assets/download/p1-4_fd5ea22.png" alt=""/>
                        </li>

                    </ul>
                </div>

                <div onClick={this.gotoIndex} className="btn">
                    立即体验
                </div>


            </div>
        )
    }


}